/**
* 系统-用户管理组件
* @module 系统
* @desc 修改添加
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" top="5vh" center width="1000px">
        <el-form :model="form" :rules="rules" label-width="130px" ref="form" size="medium" status-icon
                 v-loading="loading">
            <el-row :gutter="20">
                <el-col :span="10">
                    <el-form-item label="模板名称：" prop="templateName">
                        <el-input v-model="form.templateName" />
                    </el-form-item>
                    <el-form-item label="案件流程：" prop="caseProcess">
                        <el-select v-model="form.caseProcess" placeholder="请选择案件流程" @change="changeProcess"  class="marginleft divwidth" >
                            <el-option
                                    v-for="item in caseProcessList"
                                    :key="item.dictValue"
                                    :label="item.dictName"
                                    :value="item.dictValue"
                            >
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="对应审批名：" prop="templateApprovalNum">
                        <el-select v-model="form.templateApprovalNum" placeholder="请选择审批流程"   class="marginleft divwidth" >
                            <el-option
                                    v-for="item in flowList"
                                    :key="item.mainId"
                                    :label="item.approvalName"
                                    :value="item.mainId">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="对应文书类型：" prop="docTypeId">
                        <el-select v-model="form.docTypeId" placeholder="请选择文书类型"   class="marginleft divwidth" >
                            <el-option
                                    v-for="item in documentList"
                                    :key="item.docTypeId"
                                    :label="item.docName"
                                    :value="item.docTypeId">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="上传word模板：" prop="fileId">
                        <el-upload
                                class="upload-demo"
                                drag
                                :on-remove="()=>{form.fileId =''}"
                                :http-request="uploadWord"
                                :file-list="fileList"
                                action="#">
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="是否立即生效：" prop="isEffective">
                        <el-switch
                                :active-value="1"
                                :inactive-value="0"
                                v-model="form.isEffective">
                        </el-switch>
                    </el-form-item>
                    <el-form-item label="启用时间：" v-if="form.isEffective == 0" prop="effectiveTime">
                        <el-date-picker
                                placeholder="选择日期"
                                type="date"
                                v-model="form.effectiveTime"
                                value-format="timestamp">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="14">
                    <el-form-item label="页面模板内容：" prop="templateContent">
                        <el-input type="textarea" v-model="form.templateContent"  :rows="28"/>
                    </el-form-item>
                </el-col>
            </el-row>

        </el-form>
        <span class="dialog-footer" slot="footer">
            <el-button @click="closeDialog" size="small" type="info"><i class="iconfont icon-back"></i>取 消</el-button>
            <el-button @click="onSure()" size="small" type="primary"><i class="iconfont icon-save"></i>确 定</el-button>
        </span>
    </el-dialog>
</template>

<script>
    import {apiCaseApprovalProcessSearch, appCaseModelUpdate, apiDocumentTypeSearch,apiCaseUploadWord,apiCaseAddModel,appCaseGetModelDetail,appCaseReviseModelDetail} from "../../../../api/case";

    export default {
        name: "edit",
        props: ["show", 'type', 'title','edit'],
        data() {
            return {
                form: {
                    loginName: '',
                },
                userList: [],
                flowList:[],
                documentList:[],
                caseProcessList:[],//案件流程
                loading: false,
                options: [],
                rules: {
                    caseProcess: [
                        { required: true, message: '请选择案件流程', trigger: 'change' }
                    ],
                    templateName: [
                        { required: true, message: '请填写模板名称', trigger: 'change' }
                    ],
                    docTypeId: [
                        { required: true, message: '请选择对应文书类型', trigger: 'change' }
                    ],
                    fileId: [
                        { required: true, message: '请上传word模板', trigger: 'blur' }
                    ],
                    effectiveTime: [
                        { required: true, message: '请选择生效时间', trigger: 'change' }
                    ],
                    templateContent: [
                        { required: true, message: '请选择活动区域', trigger: 'change' }
                    ],
                }
            }
        },
        computed:{
            fileList(){
                return this.form.fileId?[{name:'word模板'+this.form.fileId}]:[]
            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },
            uploadWord(data){
                apiCaseUploadWord({file:[data.file]}).then(res=>{
                    this.form.fileId  = res.fileId;
                    this.$nextTick(()=>{
                        this.$refs['form'].validateField('fileId');
                    })
                })
            },
            changeProcess(data){
                this.form.templateApprovalNum = '';
                this.form.docTypeId = '';
                apiCaseApprovalProcessSearch({pageSize:100,pageNum:1,caseProcess:data}).then(res=>{
                    this.flowList = res.data
                });
                apiDocumentTypeSearch({pageSize:100,pageNum:1,caseProcess:data,makeUpload:2}).then(res=>{
                    this.documentList = res.data
                })
            },
            onSure() {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        if (this.type) {
                            if (this.edit) {
                                appCaseModelUpdate(this.form).then(res => {
                                    this.$message({
                                        message: '修订成功',
                                        type: 'success'
                                    });
                                    this.$emit('updateTable');
                                    this.closeDialog()
                                })
                            }else{
                                if (this.form.ifExists) {
                                    this.$confirm('该模板已有待生效版本，提交将会覆盖之前的待生效版本, 是否继续?', '提示', {
                                        confirmButtonText: '确定',
                                        cancelButtonText: '取消',
                                        type: 'warning'
                                    }).then(() => {
                                        appCaseReviseModelDetail(this.form).then(res => {
                                            this.$message({
                                                message: '修订成功',
                                                type: 'success'
                                            });
                                            this.$emit('updateTable');
                                            this.closeDialog()
                                        })
                                    })
                                }else{
                                    appCaseReviseModelDetail(this.form).then(res => {
                                        this.$message({
                                            message: '修订成功',
                                            type: 'success'
                                        });
                                        this.$emit('updateTable');
                                        this.closeDialog()
                                    })
                                }
                            }

                        } else {
                            apiCaseAddModel(this.form).then(res => {
                                this.$message({
                                    message: '新增成功',
                                    type: 'success'
                                });
                                this.$emit('updateTable');
                                this.closeDialog()
                            })
                        }
                    }

                })
            },
            initPage() {
                this.$until.workBookApi('caseProcess').then(res => {
                    this.caseProcessList = res;
                });
                if (this.type) {
                    appCaseGetModelDetail(this.type).then(res => {
                        this.form = res.data;
                        this.loading = false;
                        apiCaseApprovalProcessSearch({pageSize:100,pageNum:1,caseProcess:this.form.caseProcess}).then(res=>{
                            this.flowList = res.data
                        });
                        apiDocumentTypeSearch({pageSize:100,pageNum:1,caseProcess:this.form.caseProcess,makeUpload:2}).then(res=>{
                            this.documentList = res.data
                        })
                    })
                } else {
                    this.form = {
                        caseProcess:'',
                        docTypeId:'',
                        tempLateCode:'',
                        templateName:'',
                        templateContent:'',
                        templateApprovalNum:'',
                        isEffective:1,
                        effectiveTime:'',
                        invalidTime:'',
                        fileId:'',
                        templateVersion:'',
                        createUserId:'',
                        createTime:'',
                    };
                    this.$nextTick(() => {
                        this.loading = false;
                    })
                }
            }
        },
        watch: {
            'show'(value) {
                if (value) {
                    this.loading = true;
                    this.initPage();
                    this.$nextTick(() => {
                        this.$refs.form.clearValidate();
                    })
                }
            }
        }
    }
</script>

<style scoped>
  .tit{
      color: #B2B2B2
  }
</style>
