/**
* 系统-用户管理组件
* @module 系统
* @desc 列表
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <el-dialog :close-on-click-modal='false' title="历史版本" :visible="show" @close="closeDialog" top="5vh" center width="1000px">
        <el-table :data="list"
                  :header-cell-style="{background:'#e9eff8'}"
                  @row-click="seeFunction"
                  border
                  size="mini">
            <el-table-column align="center" label="模板名称" prop="templateName"/>
            <el-table-column align="center" label="对应流程" prop="caseProcess">
                <template slot-scope="scope">
                    {{processFilter(scope.row.caseProcess)}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="文书类型" prop="firstTime">
                <template slot-scope="scope">
                    {{scope.row.docName }}
                </template>
            </el-table-column>
            <el-table-column align="center" label="版本" prop="templateVersion"/>
            <el-table-column align="center" label="生效时间" prop="lastTime">
                <template slot-scope="scope">
                    {{scope.row.effectiveTime | formatDate}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="当前生效状态" prop="lastTime">
                <template slot-scope="scope">
                    {{scope.row.isEffective | effective }}
                </template>
            </el-table-column>
            <el-table-column align="center" label="操作" width="105">
                <template slot-scope="scope">
                    <el-tooltip :open-delay="500" content="修改" placement="top" v-if="scope.row.isEffective == 2">
                        <el-link @click.stop="onEdit(scope.row)" icon="el-icon-edit" style="margin-left: 5px"
                                 type="primary"></el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <i-edit
                :show.sync="editObj.show"
                :edit="editObj.edit"
                :title="editObj.title"
                :type="editObj.editId"
                @updateTable="getAccountList"/>
        <watch-page
                :show.sync="watchObj.show"
                :type="watchObj.editId"/>
        <lj-pagination
                :pageNum.sync="form.pageNum"
                :pageSize.sync="form.pageSize"
                :total="total"
                @getData="getAccountList"/>
    </el-dialog>
</template>

<script>
    import {apiCaseModelSearch} from "../../../../api/case";
    import LjPagination from '../../../../components/LjPagination'
    import {formatDate, YESORNO} from "../../../../until/wordBook";
    import LjSearchForm from '../../../../components/LjSearchForm'
    import iEdit from './edit'
    import watchPage from './see'

    export default {
        name: "wordModelPage",
        props:["templateCode","show"],
        data() {
            return {
                list: [],
                watchObj: {
                    show: false,
                    editId: ''
                },
                multipleSelection: [],
                total: 200,
                editObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                caseProcessList:[],
                reviseObj: {
                    show: false,
                    tempLateCode: ''
                },
                form: {
                    pageSize: 20,
                    pageNum: 1
                },
            }
        },
        components: {LjPagination, LjSearchForm, iEdit, watchPage},
        filters: {
            yesNo(value) {
                return YESORNO[value]
            },
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd hh:mm:ss')
            },
            effective(value){
                let obj={1:'生效中',2:'待生效',3:'已失效'};
                return obj[value]
            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },
            seeFunction(data) {
                this.watchObj.editId = data.loginId;
                this.watchObj.show = true
            },
            processFilter(value){
                let that = this.caseProcessList.find(i=>{return i.dictValue == value});
                if (that) {
                    return that.dictName
                }else {
                    return ''
                }
            },
            initPage() {
                this.getAccountList();
            },
            getAccountList() {
                apiCaseModelSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data
                })
            },
            onAdd() {
                this.editObj.editId = null;
                this.editObj.title = '新增模板';
                this.editObj.show = true;
            },
            onEdit(data) {
                this.editObj.editId = data.templateId;
                this.editObj.title = '修改模板';
                this.editObj.edit = true;
                this.editObj.show = true;
            }
        },
        created(){
            this.$until.workBookApi('caseProcess').then(res => {
                this.caseProcessList = res;
            });
        },
        watch: {
            'show'(value){
                if (value) {
                    this.form.templateCode = this.templateCode;
                    this.initPage()
                }
            }
        }
    }
</script>

<style lang="less" scoped>

</style>
