/**
* 系统-用户管理组件
* @module 系统
* @desc 列表
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <lj-search-form
                :onAdd="onAdd"
                :searchObj="searchObj"
                @searchList="searchList"/>
        <el-table :data="list"
                  :header-cell-style="{background:'#e9eff8'}"
                  border
                  size="mini">
            <el-table-column align="center" label="模板名称" prop="templateName"/>
            <el-table-column align="center" label="对应流程" prop="caseProcess">
                <template slot-scope="scope">
                    {{processFilter(scope.row.caseProcess)}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="文书类型" prop="firstTime">
                <template slot-scope="scope">
                    {{scope.row.docName }}
                </template>
            </el-table-column>
            <el-table-column align="center" label="版本" prop="templateVersion"/>
            <el-table-column align="center" label="生效时间" prop="lastTime">
                <template slot-scope="scope">
                    {{scope.row.effectiveTime | formatDate}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="已有待生效" prop="templateVersion">
                <template slot-scope="scope">
                    {{scope.row.ifExists ? '有' : '无' }}
                </template>
            </el-table-column>
            <el-table-column align="center" label="操作" width="80">
                <template slot-scope="scope">
                    <el-tooltip :open-delay="500" content="查看历史版本" placement="top">
                        <el-link @click.stop="viewHistory(scope.row)" icon="el-icon-search" style="margin-left: 10px"
                                 type="success"></el-link>
                    </el-tooltip>
                    <el-tooltip :open-delay="500" content="修订" placement="top">
                        <el-link @click.stop="onEdit(scope.row)" icon="el-icon-document-copy" style="margin-left: 10px"
                                 type="primary"></el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <i-edit
                :show.sync="editObj.show"
                :title="editObj.title"
                :type="editObj.editId"
                @updateTable="getAccountList"/>
        <watch-page
                :show.sync="watchObj.show"
                :type="watchObj.editId"/>
        <lj-pagination
                :pageNum.sync="form.pageNum"
                :pageSize.sync="form.pageSize"
                :total="total"
                @getData="getAccountList"/>
        <revise-component :show.sync="reviseObj.show" :templateCode="reviseObj.templateCode"/>
    </div>
</template>

<script>
    import {apiCaseModelSearch} from "../../../api/case";
    import LjPagination from '../../../components/LjPagination'
    import {formatDate, YESORNO} from "../../../until/wordBook";
    import LjSearchForm from '../../../components/LjSearchForm'
    import iEdit from './components/edit'
    import watchPage from './components/see'
    import reviseComponent from './components/reviseComponents'

    export default {
        name: "wordModelPage",
        data() {
            return {
                list: [],
                caseProcessList:[],
                watchObj: {
                    show: false,
                    editId: ''
                },
                multipleSelection: [],
                total: 200,
                editObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                reviseObj: {
                    show: false,
                    tempLateCode: ''
                },
                form: {
                    pageSize: 20,
                    isEffective:1,
                    pageNum: 1
                },
                searchObj: [
                    {
                        type: 'select',
                        options: [],
                        name: 'caseProcess',
                        label: '案件流程'
                    },
                ]
            }
        },
        components: {LjPagination, LjSearchForm, iEdit, watchPage, reviseComponent},
        filters: {
            yesNo(value) {
                return YESORNO[value]
            },
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd hh:mm:ss')
            }
        },
        methods: {
            viewHistory(data){
                this.reviseObj.templateCode = data.templateCode;
                this.reviseObj.show = true;
            },
            processFilter(value){
              let that = this.caseProcessList.find(i=>{return i.dictValue == value});
                if (that) {
                    return that.dictName
                }else {
                    return ''
                }
            },
            seeFunction(data) {
                this.watchObj.editId = data.loginId;
                this.watchObj.show = true
            },
            initPage() {
                this.$until.workBookApi('caseProcess').then(res => {
                    this.caseProcessList = res;
                    res.forEach(i=>{
                       i.label = i.dictName;
                       i.value = i.dictValue
                    });
                    this.searchObj[0].options =res
                });
                this.getAccountList();
            },
            searchList(data) {
                data.pageSize = this.form.pageSize;
                data.isEffective = this.form.isEffective;
                this.form = data;
                this.form.pageNum = 1;
                apiCaseModelSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data
                })
            },
            getAccountList() {
                apiCaseModelSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data
                })
            },
            onAdd() {
                this.editObj.editId = null;
                this.editObj.title = '新增模板';
                this.editObj.show = true;
            },
            onEdit(data) {
                this.editObj.editId = data.templateId;
                this.editObj.title = '修订模板';
                this.editObj.show = true;
            }
        },
        created() {
            this.initPage()
        }
    }
</script>

<style lang="less" scoped>

</style>
